.appIcon {
  cursor: pointer;
  position: relative;
  width: 100%;
  height: 100%;
  text-align: center;
  font-size: 16px;
  user-select: none;
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  transition: all .2s;
  .icon {
    position: absolute;
    display: inline-block;
    width: 40px;
    height: 40px;
    left: 8px;
    top: 16px;
    border-radius: 4px;
    overflow: hidden;
    border: 1px solid #fff;
    font-size: 20px;
    transition: all .2s;
    background-color: #fff;

    &>span {
      // color: #fff;
      display: flex;
      text-align: center;
      width: 100%;
      height: 100%;
      align-items: center;
      justify-content: center;
      background-size: contain;
      background-position: center;
      background-repeat: no-repeat;
    }
  }

  .name {
    position: relative;
    padding-right: 8px;
    line-height: calc(var(--app-size) - 24px);
    font-size: 16px;
    text-align: right;
    color: #fff;
    // transition: all .2s;
  }

  .desc {
    opacity: .9;
    color: #aaa;
    font-size: 12px;
    padding: 0px 8px;
    text-align: right;
    line-height: 24px;
    transition: all .2s;
  }

  .cover {
    transition: all .2s;
    position: absolute;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    background-position: center;
    background-size: cover;
    background-repeat: no-repeat;
  }

  &.appTheme {
    .name {
      opacity: 0;
      width: 0;
      height: 0;
    }

    .icon {
      width: 100%;
      height: 100%;
      left: 0;
      top: 0;
      border: none;
      font-size: 40px;
    }

    .desc {
      z-index: 1;
      text-align: center;
      padding: 0;
    }
  }
}

@media screen and (max-width: 568px) {
  .appIcon {
    font-size: 14px;
  }
}
