.main {
  display: flex;
  height: 100%;
  width: 100%;
  background-position: 50%;
  background-repeat: no-repeat;
  background-size: cover;
  position: relative;
}

.homePage {
  display: flex;
  position: relative;
  width: 100%;
  height: 100%;
  flex-direction: column;

  .homePageHeaderWrap {
    height: 6vh;
    transition: .2s;
    position: relative;
  }
  .homePageDateWrap {
    text-align: center;
  }
  .homePageSearchWrap {
    text-align: center;
    position: relative;
    z-index: 1;
    transition: .2s;
    margin-top: 0px;
  }
  .homePageAppGridWrap {
    flex: 1 1 0%;
    --app-size: 72px;
    --app-gap-x: 32px;
    --app-gap-y: 32px;
    --app-wrap-x: 76px;
    --app-number: 12;
  }
  .homePageTipsWrap {
    background: linear-gradient(rgba(0, 0, 0, 0),rgba(0, 0, 0, 0.5));
    padding-top: 20px;
    height: 60px;
    line-height: 40px;
  }
  .homePageTopButton {
    position: fixed;
    top: 13px;
    right: 13px;
    padding: 3px;
    z-index: 5;
    .item {
      cursor: pointer;
      padding: 8px;
      color: rgb(255 255 255/0.4);
      background-color: rgb(0 0 0/0);
      font-size: 28px;
      line-height: 28px;
      transition-duration: .15s;
      transition-property: all;
      transition-timing-function: cubic-bezier(.4,0,.2,1);
      border-radius: 8px;
      &:hover {
        color: rgb(255 255 255/0.9);
        background-color: rgb(0 0 0/0.2);
      }
    }
  }
  &.isMinimalism {
    .homePageHeaderWrap {
      height: 30%;
    }
    .homePageSearchWrap {
      margin-top: 20px;
    }
  }
}

@media screen and (max-width: 568px) {
  .homePage {
    .homePageHeaderWrap {
      height: 40px;
    }
    .homePageAppGridWrap {
      --app-wrap-x: 24px;
      --app-size: 64px;
      --app-gap-x: 28px;
      --app-gap-y: 28px;
      --app-number: 4;
    }
  }
}
