.bar {
  width: 52px;
  max-height: 70%;
  padding: 4px 0;
  background-color: rgba(0, 0, 0, 0.4);
  border-radius: 16px;
  display: flex;
  flex-direction: column;
  align-items: center;
  user-select: none;
  backdrop-filter: saturate(200%) blur(40px);

  .content,
  .extra,
  .divider {
    width: 100%;
    position: relative;
  }

  .content {
    flex-grow: 1;
    overflow-y: auto;
    margin-left: -60px;
    width: 110px;
    pointer-events: none;
    padding: 4px 0 4px 60px;

    &:hover {
      .group .item .name {
        opacity: 1;
      }
    }

    &::-webkit-scrollbar {
      display: none;
    }
  }

  .extra {
    flex-shrink: 0;
    pointer-events: auto;
    padding: 4px 0;
  }

  .divider {
    background-color: rgba(0, 0, 0, 0.15);
    border-radius: 1px;
    flex-shrink: 0;
    width: 22px;
    height: 2px;
    margin-top: 4px;
    margin-bottom: 4px;
  }

  .group {
    position: relative;
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
    pointer-events: auto;
  }

  .item {
    display: flex;
    width: 36px;
    height: 36px;
    transition-duration: .15s;
    transition-property: color, background-color, border-color, text-decoration-color, fill, stroke;
    transition-timing-function: cubic-bezier(.4, 0, .2, 1);
    color: rgba(255, 255, 255, 0.6);
    border-radius: 8px;
    cursor: pointer;
    padding: 4px;
    margin: 4px;
    font-size: 24px;
    align-items: center;
    justify-content: center;

    &:hover,
    &.active {
      color: #fff;
      background-color: rgba(255, 255, 255, 0.15);
    }

    &.active {
      box-shadow: 0 0 #0000, 0 0 #0000, 0px 4px 12px -4px #0006;
    }

    .name {
      position: absolute;
      transition: all .2s;
      align-items: center;
      padding-left: 8px;
      padding-right: 4px;
      font-size: 12px;
      color: #fff;
      text-shadow: 0 0 4px #0000004d;
      right: 58px;
      border-radius: 4px;
      white-space: nowrap;
      line-height: 24px;
      opacity: 0;
      pointer-events: none;
    }

    &:hover {
      .name {
        text-shadow: none;
        background-color: rgba(255, 255, 255, 0.9);
        color: rgb(58, 58, 60);
        padding-right: 8px;
        box-shadow: 0 0 #0000, 0 0 #0000, 0px 2px 12px 0px #00000026;
      }
    }
  }
}

@media screen and (max-width: 568px) {
  .bar {
    width: 42px;
    background-color: rgba(0, 0, 0, 0.75);
    // border-radius: 100% 0 0 100%;
    backdrop-filter: none;
    .item {
      width: 28px;
      height: 28px;
      font-size: 18px;
    }
  }
}
