.container {
  width: 100%;
  height: 100%;
  display: flex;
  cursor: pointer;
  background-position: center;
  background-size: cover;
  background-repeat: no-repeat;

  .mainPanel {
    // background-color: #fff;
    align-items: center;
    width: 100%;
    height: 100%;
    flex-shrink: 0;
    display: flex;
    flex-direction: column;

    .content {
      width: 100%;
    }

    &.needSubPanel {
      align-content: start;
      text-align: left;
      border-right: 1px solid rgba(0, 0, 0, 0.05);
      background-color: rgb(248, 248, 248);
    }
  }

  .subPanel {
    background-color: #fff;
    display: flex;
    align-items: center;
    justify-content: center;
    text-align: center;
    flex-flow: wrap;
    width: 100%;
    height: 100%;
  }
}

.iconWrap {
  display: flex;
  width: 100%;
  font-size: 32px;
  span {
    padding: 4px;
    background: var(--app-theme-color);
    border-radius: 4px;
    color: #fff;
  }
}


.flex {
  display: flex;
}

.fw-nowrap {
  flex-wrap: nowrap;
}

.row {
  flex-direction: row !important;
}

.column {
  flex-direction: column !important;
}

.flex-grow {
  flex-grow: 1;
}

.jc-center {
  justify-content: center;
}

.jc-evenly {
  justify-content: space-evenly;
}

.jc-between {
  justify-content: space-between;
}

.jc-start {
  justify-content: flex-start;
}

.jc-end {
  justify-content: flex-end;
}

.ai-center {
  align-items: center;
}

.ai-baseline {
  align-items: baseline;
}

.ac-start {
  align-content: flex-start;
}

.f12 {
  font-size: 12px;
}

.f14 {
  font-size: 14px;
}

.f16 {
  font-size: 16px;
}

.f24 {
  font-size: 24px;
}

.f36 {
  font-size: 36px;
}

.w-full {
  width: 100%;
}

.f48 {
  font-size: 48px;
}
