.container {
  width: 100%;
  height: 100%;
  display: flex;
  cursor: pointer;
  --app-theme-color: rgb(74, 122, 255);

  .mainPanel {
    background-color: #fff;
    align-items: center;
    width: 100%;
    height: 100%;
    flex-flow: wrap;
    flex-shrink: 0;
    display: flex;

    .miniHeader {
      width: 100%;
      text-align: center;
      line-height: 28px;
      background-color: var(--app-theme-color);
      color: #fff;
    }

    &.needSubPanel {
      align-content: start;
      text-align: left;
      border-right: 1px solid rgba(0, 0, 0, 0.05);
      background-color: rgb(248, 248, 248);
    }
  }

  .subPanel {
    background-color: #fff;
    display: flex;
    align-items: center;
    flex-flow: wrap;
    width: 100%;
    height: 100%;

  }
}

.todoPanel {
  height: 100%;
  width: 100%;
  padding: 4%;
  .todoTitle {
    font-size: 12px;
    margin-bottom: 8px;
  }
  .todoList {
    display: flex;
    flex-direction: column;
    .todoItem {
      display: flex;
      align-items: center;
      overflow: hidden;
      text-overflow: ellipsis;
      white-space: nowrap;
      padding: 0px 0 8px 4px;
      font-size: 12px;
      .tag {
        width: 4px;
        height: 10px;
        border-radius: 2px;
        margin-right: 8px;
        flex-shrink: 0;
        background-color: var(--app-theme-color);
      }
    }
  }
}

.iconWrap {
  display: flex;
  width: 100%;
  font-size: 36px;
  span {
    padding: 4px;
    background: var(--app-theme-color);
    border-radius: 4px;
    color: #fff;
  }
}


.flex {
  display: flex;
}

.fw-nowrap {
  flex-wrap: nowrap;
}

.row {
  flex-direction: row !important;
}

.column {
  flex-direction: column !important;
}

.flex-grow {
  flex-grow: 1;
}

.jc-center {
  justify-content: center;
}

.jc-evenly {
  justify-content: space-evenly;
}

.jc-between {
  justify-content: space-between;
}

.jc-start {
  justify-content: flex-start;
}

.jc-end {
  justify-content: flex-end;
}

.ai-center {
  align-items: center;
}

.ai-baseline {
  align-items: baseline;
}

.ac-start {
  align-content: flex-start;
}

.f12 {
  font-size: 12px;
}

.f14 {
  font-size: 14px;
}

.f16 {
  font-size: 16px;
}

.f24 {
  font-size: 24px;
}

.f36 {
  font-size: 36px;
}

.w-full {
  width: 100%;
}

.f48 {
  font-size: 48px;
}

.tc-theme-color {
  color: var(--app-theme-color);
}
