.container {
  display: flex;
  position: relative;
  flex-direction: column;
  height: 100%;
  overflow: hidden;
  .browserHeader {
    width: 100%;
    padding: 6px 0;
    background-color: rgba(230, 230, 230, 0.7);
    color: rgba(161, 161, 161, 1);
    .withUrl {
      display: flex;
      align-items: center;
      justify-content: center;
      height: 20px;
      padding: 0px 4px;
      font-size: 12px;
      font-weight: 500;
    }
  }
  .browserMockup {
    flex: 1;
    font-size: 0;
    iframe {
      width: 100%;
      border: 0;
      height: 100%;
      transition: all 0.3s;
    }
  }
}
