.time {
  font-size: 70px;
  font-family: HarmonyOS_Sans;
  user-select: none;
  font-weight: 400;
  text-shadow: 0 2px 6px rgba(0, 0, 0, 0.6);
  color: #fff;
  line-height: 70px;
  display: inline-block;
  text-align: center;
  time {
    display: inline-block;
    width: 42px;
    text-align: center;
  }
  .colon {
    vertical-align: calc(70px / 12);
  }
  &.isMinimalism {
    font-size: 130px;
    line-height: 100px;
    time {
      width: 78px;
    }
    .colon {
      vertical-align: calc(100px / 12);
    }
  }
}
.date {
  font-size: 14px;
  line-height: 26px;
  opacity: 0.88;
  margin-top: -3px;
  text-shadow: 0 2px 6px rgba(0, 0, 0, 0.3);
  color: #fff;
  text-align: center;
  user-select: none;
  & > span {
    margin: 0 2px;
  }
  &.isMinimalism {
    font-size: 32px;
    line-height: 39px;
    margin-top: 11px;
  }
}

@media screen and (max-width: 568px) {
  .time {
    &.isMinimalism {
      font-size: 90px;
      time {
        width: 53px;
      }
    }
  }
}
