.container {
  height: 100%;
  width: 100%;
  overflow: auto;
  position: relative;
  background-color: #fff;

  :global {
    .eleven-os-spin-nested-loading {
      height: 100%;
      width: 100%;

      .eleven-os-spin {
        max-height: initial;
      }
    }

    .eleven-os-spin-container {
      height: 100%;
      width: 100%;

      #micro-app-container-r3f {
        height: 100%;
        width: 100%;

        &>div {
          height: 100%;
          width: 100%;
        }
      }
    }
  }
}
